.techstack-section {
    background-color: #0d0d0d;
    color: #f5f5f5;
    padding: 40px 20px; /* パディングを少し小さく */
    text-align: center;
}

.techstack-title {
    font-size: 2rem; /* フォントサイズを小さく */
    margin-bottom: 30px; /* マージンを調整 */
    border-bottom: 2px solid #00d8ff;
    display: inline-block;
    padding-bottom: 6px; /* パディングを少し小さく */
}

.techstack-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr)); /* カラムの幅を小さく */
    gap: 16px; /* ギャップを縮める */
    max-width: 900px; /* 幅を少し狭く */
    margin: 0 auto;
}

.techstack-item {
    background-color: #1a1a1a;
    border: 1px solid #333;
    border-radius: 12px;
    padding: 15px; /* パディングを小さく */
    transition: transform 0.3s ease;
}

.techstack-item:hover {
    transform: translateY(-5px);
    border-color: #00d8ff;
}

.techstack-icon {
    font-size: 2rem; /* アイコンのサイズを小さく */
    margin-bottom: 8px; /* 下のマージンを小さく */
}

.techstack-item h3 {
    font-size: 1rem; /* 見出しのフォントサイズを小さく */
    color: #00d8ff;
    margin-bottom: 6px; /* 下のマージンを小さく */
}

.techstack-item p {
    font-size: 0.85rem; /* 説明文のフォントサイズを小さく */
    color: #ccc;
}
