.header {
    background-color: #0d0d0d;
    color: #f5f5f5;
    padding: 20px;
    text-align: center;
    position: sticky;
    top: 0;
    z-index: 1000;
  }
  
  .navbar ul {
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: center;
  }
  
  .navbar ul li {
    margin: 0 20px;
  }
  
  .navbar ul li a {
    color: #f5f5f5;
    text-decoration: none;
    font-size: 1.1rem;
  }
  
  .navbar ul li a:hover {
    color: #00d8ff;
  }
  