.profile-section {
    padding: 60px 20px;
    background-color: #111;
    color: #fff;
    text-align: center;
  }
  
  .profile-title {
    font-size: 2rem;
    margin-bottom: 20px;
    border-bottom: 2px solid #444;
    display: inline-block;
    padding-bottom: 5px;
  }
  
  .profile-card {
    max-width: 600px;
    margin: 0 auto;
    background-color: #222;
    padding: 30px;
    border-radius: 12px;
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.1);
    text-align: left;
    line-height: 1.8;
  }
  