.skills-section {
    background-color: #111;
    color: #fff;
    padding: 60px 20px;
    text-align: center;
  }
  
  .skills-title {
    font-size: 2rem;
    margin-bottom: 30px;
    border-bottom: 2px solid #444;
    display: inline-block;
    padding-bottom: 5px;
  }
  
  .skills-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
    gap: 20px;
    max-width: 800px;
    margin: 0 auto;
  }
  
  .skill-card {
    background-color: #222;
    padding: 15px 10px;
    border-radius: 8px;
    box-shadow: 0 0 6px rgba(255, 255, 255, 0.05);
    transition: transform 0.2s ease;
    font-weight: 500;
  }
  
  .skill-card:hover {
    transform: scale(1.05);
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.15);
  }

  .skills-section {
    padding: 4rem 2rem;
    background-color: #111;
    color: #fff;
    text-align: center;
  }
  
  .section-title {
    font-size: 2rem;
    margin-bottom: 2rem;
    border-bottom: 2px solid #444;
    display: inline-block;
    padding-bottom: 0.5rem;
  }
  
  .skills-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 1.5rem;
    justify-items: center;
    align-items: center;
  }
  
  .skill-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
    font-size: 1rem;
  }
  